
table.paginated-table {
  margin-top: 2rem;
  table-layout: fixed;
}
table.paginated-table .icon {
    font-size: 16px;
}
table.paginated-table .sorted-column {
    background: #EEE;
}
table.paginated-table th.sortable:hover {
    background: #EEE;
}
.pagination-list li.number {
  background-color: white;
  border-radius: 0px;
  margin-right: 0rem;
  min-width: 0em;
  padding: 0rem;
}
.pagination-list li.number a {
    font-size: 1rem;
}
.pagination-list .disabled {
  background: #dbdbdb;
  opacity: 0.5;
  pointer-events: none;
}
.pagination-list .disabled a {
    color: #7a7a7a;
    font-size: 1rem;
}
.pagination-list .pagination-next,
.pagination-list .pagination-previous {
  width: auto;
  font-size: 1rem;
}
.pagination-list .pagination-next .pagination-link,
  .pagination-list .pagination-previous .pagination-link {
    border: none;
}
.pagination-list .pagination-previous {
  -webkit-box-ordinal-group: 1 !important;
      -ms-flex-order: 0 !important;
          order: 0 !important;
}
.pagination-list li.active a {
  font-size: 1rem;
  background-color: #00d1b2;
  border-color: #00d1b2;
  color: white;
}



